<template>
  <div class="selected-png">
    <!-- 导航栏 start-->
    <div class="top-menu">
      <i @click="drawer = true" class="el-icon-s-tools"></i>
      <el-drawer
        size="60%"
        title="我是标题"
        :visible.sync="drawer"
        :with-header="false"
      >
        <span>我来啦!</span>
      </el-drawer>
    </div>
    <!-- 导航栏 end-->
    <div class="pngbox" @click="drawer = false">
      <el-carousel
        ref="mycarousel"
        arrow="always"
        :autoplay="false"
        :loop="false"
        @change="test"
        :initial-index="nowPng"
        indicator-position="none"
      >
        <el-carousel-item v-for="item in imgArr" :key="item">
          <div class="png-box">
            <div><img :src="item" alt="" /></div>
          </div>
        </el-carousel-item>
      </el-carousel>
    </div>

    <!-- 放弃滑动改为轮播图 -->
  </div>
</template>

<script>
export default {
  data() {
    return {
      count: 3,
      disabled: false,
      imgArr: [],
      nowPng: 0,
      drawer: false,
    };
  },

  created() {
    this.getOnePng();
    this.getOnePng();
  },

  mounted() {},

  methods: {
    async getOnePng() {
      var res = null;
      res = await this.$axios.get(
        "https://790d5b85-9674-4a89-9bcc-c0657ea369be.bspapp.com/mainFun/hello/hello"
      );
      this.imgArr = [...this.imgArr, res.data.headers.location];
      console.log("图片加载", this.imgArr);
    },

    test(key1, key2) {
      //key1 为当前激活的参数
      //key2 为之前的参数
      if (key1 + 1 === this.imgArr.length) {
        this.getOnePng();
        this.nowPng = this.imgArr.length - 1;
        this.$refs.mycarousel.setActiveItem(this.imgArr.length - 1);
      }

      console.log(key1, key2);
    },
  },
};
</script>

<style>
/* 导航栏start */
.top-menu {
  padding: 0 15px;
  box-sizing: border-box;
  /* position: fixed;
  top: 0;
  left: 0; */
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: right;
  background-color: black;
  overflow: auto;
}

.top-menu .el-icon-s-tools {
  color: white;
  font-size: 25px;
}
/* 导航栏 end */
.el-carousel {
  height: calc(100vh - 90px);
}
.el-carousel__container {
  height: calc(100vh - 90px) !important;
}

.el-carousel__item:nth-child(2n) {
  background-color: #000;
}

.el-carousel__item:nth-child(2n + 1) {
  background-color: #000;
}

/* img style */
.selected-png .png-box {
  display: flex;
  justify-content: center;
  align-items: center;
  /* line- */
  /* position: relative; */
  height: calc(100vh - 90px);
}
.selected-png .png-box img {
  /* position: absolute;
  top: 50%;
  left: 50%; */
  /* margin-left: -50%; */
  /* margin-top: 50%; */
  display: block;
  width: 100%;
  height: auto;
}
.el-drawer__body {
  /* z-index: 999; */
}
/* img end */
</style>